          <th></th>
          <th></th>
          <th></th>
          <tr>
            <td>
              <div class="hr_img">
                <img :src="this.list.firstimage" class="imgg" />
              </div>
              <span class="vertitle">{{ this.list.title }}</span>
            </td>
            <td>{{ this.select }}</td>
            <td>{{ this.value[0] }}<br />~ {{ this.value[1] }}</td>
            <td>{{ this.number }}</td>
            <td>{{ this.price }}</td>
          </tr>
        </table>
        <h3>   </h3>
        <p> </p>
      </div>
      <div class="jin_left">
        <div class="left5">
          <p class="ib_left"> </p>
          <span class="font_mini"
            >/  <span class="red"> * </span></span
          >
          <div class="block321">
            <el-date-picker
              v-model="value"
              type="daterange"
              start-placeholder="Start date"
              end-placeholder="End date"
              :default-time="defaultTime"
              value-format="YYYY-MM-DD"
            ></el-date-picker>
            <p class="inf">   </p>
          </div>
          <div>
            <p class="font_mini">  <span class="red"> * </span></p>
            <select name="items1" class="select" v-model="select">
              <option value=""></option>
              <option value=""></option>
              <option value=""></option>
            </select>
          </div>
          <p class="font_mini"> <span class="red"> * </span></p>
          <input type="number" class="numberbox" v-model="number" />
          <p class="inf">   .</p>
          <div>
            <p class="customer ib_left"></p>
            <p class="name font_mini"> <span class="red"> * </span></p>

            <input
              type="text"
              v-model="firstname"
              class="name1"
              placeholder=""
            />
            <input
              type="text"
              v-model="lastname"
              class="name2"
              placeholder=""
            />
            <p class="phone font_mini">
                <span class="red"> * </span>
            </p>
            <input
              v-model="phone"
              type="text"
              class="name3"
              placeholder=" '-'  "
            />
          </div>
        </div>
        <div class="right5">
          <div class="ib_top">
            <p class="ib_right"><span class="red"> * </span> </p>
          </div>
          <p class="datenum font_mini"></p>
          <input
            disabled
            class="datepiceer"
            type="text"
            :value="this.value[0] + ' ~ ' + this.value[1]"
          />

          <p class="font_mini addoption"></p>
          <div class="plus_box">
            <input type="checkbox" name="xxx" value="yyy" /><label></label
            ><br />
            <input type="checkbox" name="xxx" value="yyy" /><label
              ></label
            >
          </div>
          <div>
            <br />
            <p class="font_mini"></p>
            <input type="text" class="mail" placeholder="E-MAIL" />
          </div>
        </div>

        <div class="last_box">
          <p class="ib_left ib_plus"></p>
          <textarea class="area"></textarea>
        </div>
      </div>
      <div class="jin_right">
        <div class="fixbox">
          <div class="center">
            <p class="p1"></p>

            <div class="hr2"></div>
          </div>
          <div class="hr2_pa">
            <span class="p2"> </span
            ><span class="p2_right">{{ this.price }}0,000</span><br />
            <span class="p2"></span><span class="p2_right">0</span><br />
          </div>
          <div class="gray">
            <p> </p>
            <div class="g_right">{{ this.price }}0,000</div>
          </div>
          <div class="btnbox">
            <button class="ribtn" @click="requestPay"></button>
          </div>
        </div>
      </div>
      <div class="both"></div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import { defineComponent, ref } from "vue";
export default defineComponent({
  watch: {
    select: async function () {
      if (this.select === "") {
        this.price + 1;
        console.log(this.price);
      }
    },
  },
  async created() {
    console.log(this.select);
    this.$emit("searchon", true);
    await this.refresh();
  },
  setup() {
    const value = ref("00");
    const defaultTime = ref([
      new Date(2000, 1, 1, 0, 0, 0),
      new Date(2000, 2, 1, 23, 59, 59),
    ]); // '00:00:00', '23:59:59'

    return {
      value,
      defaultTime,
    };
  },
  data() {
    return {
      phone: "",
      firstname: "",
      lastname: "",
      price: "",
      list1: "",
      number: "",
      select: "",
      list: [],
      impCode: "imp00284079",
      order: {
        name: "DSTRAVEL",
        amount: 1000,
        buyer_tel: "010-5195-1648",
      },
      token: sessionStorage.getItem("TOKEN"),
    };
  },
  methods: {
    async refresh() {
      const url1 = `/REST/travel/selectone?contentId=${this.$route.query.code}`;
      const headers1 = { "Content-type": "application/json" };

      const response1 = await axios.get(url1, { headers1 });
      console.log(response1);
      this.list = response1.data.TD;
      this.price = Number(response1.data.TD.price);
      console.log(this.price);
    },
    requestPay: async function () {
      if (this.value === "00") {
        alert(" ");
        return;
      }
      if (this.select === "") {
        alert(" ");
        return;
      }
      if (this.number === "") {
        alert(" ");
        return;
      }
      if (this.firstname === "") {
        alert(" ");
        return;
      }
      if (this.lastname === "") {
        alert(" ");
        return;
      }
      if (this.phone === "") {
        alert(" ");
        return;
      }

      //1.   (  )
      var IMP = window.IMP;
      IMP.init(this.impCode);
      //3.  
      IMP.request_pay(
        {
          pg: "html5_inicis",
          pay_method: "card",
          merchant_uid: "merchant_" + new Date().getTime(),
          name: this.order.name,
          amount: this.order.amount,
          buyer_tel: this.order.buyer_tel,
        },
        function (rsp) {
          if (rsp.success) {
            //4.    () 
            //ajax    -> 5.  validation check

            //6.      4.        
            var msg = " .";
            msg += "ID : " + rsp.imp_uid;
            msg += " ID : " + rsp.merchant_uid;
            msg += "  : " + rsp.paid_amount;
            msg += "  : " + rsp.apply_num;
          } else {
            var msg1 = " .";
            msg1 += " : " + rsp.error_msg;
          }
          console.log(msg);
          console.log(msg1);
        }
      );
    },
  },
});
</script>
